import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Book } from 'src/app/classes/book';
import { Editor } from 'src/app/classes/editor';
import { BookService } from 'src/app/services/book.service';

@Component({
  selector: 'app-add-book-form',
  templateUrl: './add-book-form.component.html',
  styleUrls: ['./add-book-form.component.scss']
})
export class AddBookFormComponent implements OnInit {

  private _editorOptions = [];
  private _form: AddFormModel = new AddFormModel();
  
  constructor(private bookService : BookService,private router : Router) {
   }

  ngOnInit(): void {
    this.editorOptions = Object.entries(Editor);
  }

  submitForm() {
    let title = this.form.title;
    let author = this.form.author;
    let pages = this.form.pages;
    let editor = this.form.editor;

    let book = new Book(this.form.title,this.form.author,this.form.pages,this.form.editor);
    this.bookService.addBook(book);
    this.form = new AddFormModel();
    this.router.navigate(['/']);
  }

  public get editorOptions() {
    return this._editorOptions;
  }
  public set editorOptions(value) {
    this._editorOptions = value;
  }

  public get form(): AddFormModel {
    return this._form;
  }
  public set form(value: AddFormModel) {
    this._form = value;
  }

}

class AddFormModel {

  private _title: String;
  private _author: String;
  private _pages: Number;
  private _editor: Editor;
  
  public get title(): String {
    return this._title;
  }
  public set title(value: String) {
    this._title = value;
  }

  public get author(): String {
    return this._author;
  }
  public set author(value: String) {
    this._author = value;
  }

  public get pages(): Number {
    return this._pages;
  }
  public set pages(value: Number) {
    this._pages = value;
  }

  public get editor(): Editor {
    return this._editor;
  }
  public set editor(value: Editor) {
    this._editor = value;
  }




}
