import { Component, OnInit } from '@angular/core';
import * as confetti from 'canvas-confetti';


@Component({
  selector: 'app-confetti',
  templateUrl: './confetti.component.html',
  styleUrls: ['./confetti.component.scss']
})
export class ConfettiComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {
    var myCanvas = document.getElementById('my-confetti');
    let myConfetti = confetti.create(myCanvas, {
      resize: true,
      useWorker: true
    });

    let duration = 15 * 1000;
    let animationEnd = Date.now() + duration;
    let timeLeft = duration;
    let skew = 1;

    requestAnimationFrame(this.doConfetti(myConfetti, duration, timeLeft, animationEnd, skew));
  }


  doConfetti(myConfetti, duration, timeLeft, animationEnd, skew): FrameRequestCallback {
    let me = this;
    return () => {
      timeLeft = animationEnd - Date.now();
      skew = Math.max(0.8, skew - 0.001);
      let ticks: number = Math.max(200, 500 * (timeLeft / duration));

      myConfetti({
        particleCount: 1,
        startVelocity: 0,
        ticks: ticks,
        gravity: 0.5,
        origin: {
          x: Math.random(),
          y: (Math.random() * skew) - 0.2
        },
        colors: [me.randomColor()]
      });


      //if (timeLeft > 0) {
      requestAnimationFrame(me.doConfetti(myConfetti, duration, timeLeft, animationEnd, skew));
      //}
    };
  }

  randomColor(): String {
    let colors = [
      '#26ccff',
      '#a25afd',
      '#ff5e7e',
      '#88ff5a',
      '#fcff42',
      '#ffa62d',
      '#ff36ff'
    ];
    return colors[Math.floor(Math.random() * colors.length)];

  }

}
