import { Component, OnInit } from '@angular/core';
import { Book } from 'src/app/classes/book';
import { BookService } from 'src/app/services/book.service';
import { SortState } from './../../classes/sort-state';

@Component({
  selector: 'app-list-book',
  templateUrl: './list-book.component.html',
  styleUrls: ['./list-book.component.scss']
})
export class ListBookComponent implements OnInit {

  private _bookList: Array<Book>;
  private _sortByTitle: SortState = SortState.no;
  private _sortByAuthor: SortState = SortState.no;
  private _sortByPages: SortState = SortState.no;
  private _sortByEditor: SortState = SortState.no;
  
  constructor(private bookService : BookService) {
    this.bookList = this.bookService.listAll();
    this.bookService.newDataEvent.subscribe(()=>{
      this.bookList = this.bookService.listAll();
    });
    this.bookService.filterEvent.subscribe((filteredBookList)=>{
      this.bookList = filteredBookList;
    });
   }

  ngOnInit(): void {
  }

  sortList(field : String) : void {
    switch(field){
      case 'title' : {
        let currentState = this.sortByTitle;
        this.resetSort();
        this.sortByTitle = this.toggleState(currentState);
        if(this.sortByTitle===SortState.asc){
          this.bookList.sort((b1,b2)=>this.compareString(b1.title,b2.title));
        }else {
          this.bookList.sort((b1,b2)=>-1*this.compareString(b1.title,b2.title));
        }
        break;
      }
      case 'author' : {
        let currentState = this.sortByAuthor;
        this.resetSort();
        this.sortByAuthor = this.toggleState(currentState);
        if(this.sortByAuthor===SortState.asc){
          this.bookList.sort((b1,b2)=>this.compareString(b1.author,b2.author));
        }else {
          this.bookList.sort((b1,b2)=>-1*this.compareString(b1.author,b2.author));
        }
        break;
      }
      case 'pages' : {
        let currentState = this.sortByPages;
        this.resetSort();
        this.sortByPages = this.toggleState(currentState);
        if(this.sortByPages===SortState.asc){
          this.bookList.sort((b1,b2)=>this.compareNumber(b1.pages,b2.pages));
        }else {
          this.bookList.sort((b1,b2)=>-1*this.compareNumber(b1.pages,b2.pages));
        }
        break;
      }
      case 'editor' : {
        let currentState = this.sortByEditor;
        this.resetSort();
        this.sortByEditor = this.toggleState(currentState);
        if(this.sortByEditor===SortState.asc){
          this.bookList.sort((b1,b2)=>this.compareString(b1.editor,b2.editor));
        }else {
          this.bookList.sort((b1,b2)=>-1*this.compareString(b1.editor,b2.editor));
        }
        break;
      }
    }
  }

  resetSort() : void {
    this.sortByTitle = SortState.no;
    this.sortByAuthor = SortState.no;
    this.sortByPages = SortState.no;
    this.sortByEditor = SortState.no;
  }

  isNo(field:string) : boolean {
    return this.isState(field,SortState.no);
  }

  isAsc(field:string) : boolean {
    return this.isState(field,SortState.asc);
  }

  isDesc(field:string) : boolean {
    return this.isState(field,SortState.desc);
  }

  private isState(field:String,state:SortState) : boolean{
    let result : boolean;
    switch(field){
      case 'title' : {
        result = this.sortByTitle===state;
        break;
      }
      case 'author' : {
        result = this.sortByAuthor===state;
        break;
      }
      case 'pages' : {
        result = this.sortByPages===state;
        break;
      }
      case 'editor' : {
        result = this.sortByEditor===state;
        break;
      }
      
    }
    return result;
  }

  private compareString(s1,s2) {
    if(s1 < s2) { return -1; }
    if(s1 > s2) { return 1; }
    return 0;
  }

  private compareNumber(n1:Number,n2:Number) {
    if(n1 < n2) { return -1; }
    if(n1 > n2) { return 1; }
    return 0;
  }

  private toggleState(state:SortState) {
    let result : SortState;
    switch(state){
      case SortState.asc : {
        result = SortState.desc;
        break;
      }
      case SortState.desc : {
        result = SortState.asc;
        break;
      }
      case SortState.no : {
        result = SortState.asc;
        break;
      }
    }
    return result;
  }
  

  public get bookList(): Array<Book> {
    return this._bookList;
  }
  public set bookList(value: Array<Book>) {
    this._bookList = value;
  }

  public get sortByTitle(): SortState {
    return this._sortByTitle;
  }
  public set sortByTitle(value: SortState) {
    this._sortByTitle = value;
  }

  public get sortByAuthor(): SortState {
    return this._sortByAuthor;
  }
  public set sortByAuthor(value: SortState) {
    this._sortByAuthor = value;
  }

  public get sortByPages(): SortState {
    return this._sortByPages;
  }
  public set sortByPages(value: SortState) {
    this._sortByPages = value;
  }

  public get sortByEditor(): SortState {
    return this._sortByEditor;
  }
  public set sortByEditor(value: SortState) {
    this._sortByEditor = value;
  }

}
